.LoginHeader {
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7f1085;
}

.LoginLogo {
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #fff;
}

.LoginLogo img {
  width: 120px;
  height: 84.3px;
}

.Contents {
  display: flex;
  justify-content: center;
}

.ContentBox {
  position: relative;
  top: -55px;
  width: 23.75em;
  height: 37vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-color: #fff;
  border-radius: 5%;
  box-shadow: 3px 3px 3px #c2c2c2;
  background-color: #fff;
}

.Content_getInfo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.0em;
  margin-bottom: 1.0em;
}

.Content_getInfo img {
  margin-right: 1.25em;
}

.getInput {
  border-top: 0em;
  border-left: 0em;
  border-right: 0em;
}
.button {
  display:flex;
  width: 150px;
  height: 50px;
  color: #fff;
  align-items: center;
  justify-content: center;
  font-size: 13pt;
  background-color: #7f1085;
}
